<template>
  <div class="login">
     <div class="use-pic">
              <img src="static/login/xxn.png" alt="">
     </div>
    <van-cell-group>
      <van-field v-model="user.name" clearable label="用户" right-icon="question-o" placeholder="请输入用户名"
        @click-right-icon="$toast('question')" label-width="60px"/>

      <van-field v-model="user.password" type="password" label="密码" placeholder="请输入密码" label-width="60px" />
    </van-cell-group>
     <van-button type="info" class="submit" @click="login()">信息按钮</van-button>
  </div>
</template>
<script>
import {Field,Button} from 'vant';
  export default {
    data() {
      return {
          user:{
              name:"hsh",
              passwrod:"hsh123456"
          }
      }
    },
    components:{
         [Field.name]:Field,
         [Button.name]:Button
    },
    methods:{
        login(){
            if(this.user.name=="hsh"&&this.user.passwrod=="hsh123456"){
                this.$router.push({ path: '/home' })
            }
        }
    }
  }

</script>
<style>
.login{
    margin-top: 100px;
}
.use-pic{
    text-align: center;
    height: 60px;
    margin-bottom: 15px;
}
.use-pic>img{
    height: 60px;
}
.submit{
    width: 80%;
    height: 40px;
    margin-top: 15px;
}
</style>




